import React from 'react';
import PropTypes from 'prop-types';

import Portal from '../portal/Portal';
import Icon from '../icon/Icon';
import Button from '../button/Button';
import styles from'./AddingForm.module.css';
import { useSelector } from 'react-redux';
import AdminEditMode from '../CommonWindow/AdmiEditMode';



const AddingForm = ({
  isOpen, onCancel, onSubmit, description
}) => {
  const userIsAuth = useSelector(store => store.loginPage.userIsAuth)
  const adminIsAuth = useSelector(store => store.loginPage.adminIsAuth)
  const approved = useSelector(store => store.loginPage.approved)


  return (
    <>
      {isOpen &&
        <Portal>
          <div className={styles.modalOverlay}>
            <div className={styles.modalWindow}>
              <div className={styles.modalHeader}>
                <div className={styles.modalTitle}>    </div>
                <Icon name="times" onClick={onCancel} />
              </div>
              <div className={styles.modalBody}>
              <div className={styles.modalTitle}>    {description.nominal} {description.nominal === 1 && ''}{description.nominal === 3 && ''}{description.nominal === 5 && ''}
                  {description.nominal === 10 && ''}{description.nominal === 25 && ''}{description.nominal === 50 && ''}{description.nominal === 100 && ''} {description.year}  {description.upravl}-{description.director}-{description.kassir}
                  </div>
                  
    { !userIsAuth && !adminIsAuth &&<div>  ,     </div>}
                  
    { userIsAuth && !adminIsAuth && <div>   </div>}
                  
    { approved && userIsAuth && !adminIsAuth &&
                  <div>
                <div>  Perekupi.ru     
                     . 
                              .
                  <br/><b>      :</b>
                </div>
                <div><textarea style={{width: '80%', marginTop: '10px', border: '1px solid black', height: '60px'}}></textarea></div>
                <div style={{ marginTop: '10px', marginBottom: '10px'}}>   .    3 : 
                  ,    .
                </div>
                <Button onClick={onSubmit} > </Button>
                <Button onClick={onSubmit} ></Button>
                      </div>}

    { adminIsAuth &&
                  <div>

                    <AdminEditMode description = {description}/>
                    
                      </div>}
              </div>
              <div className={styles.modalFooter}>
                <Button onClick={onCancel} invert></Button>
                

              </div>
            </div>
          </div>
        </Portal>
      }
    </>
  );
};

AddingForm.propTypes = {
  title: PropTypes.string,
  info: PropTypes.string,
  isOpen: PropTypes.bool,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
  children: PropTypes.node,
};

AddingForm.defaultProps = {
  title: 'Modal title',
  
  isOpen: true,
  onCancel: () => { },
  onSubmit: () => { },
  children: null,
};

export default AddingForm;
